import React, { useState } from 'react';
import { Tab, Tabs } from 'react-bootstrap';

import styled from 'styled-components';
import ContaactsTab from './ContaactsTab';

const MyTabs = (props = {}) => {
  const [key, setKey] = useState('contacts');

  const renderTabContent = (passed) => {
    return <TabContent>{`< ${passed} >`}</TabContent>;
  };

  return (
    <TabsStyle id='controlled-tab' activeKey={key} onSelect={(k) => setKey(k)}>
      <Tab eventKey='contacts' title='CONTACTS'>
        <TabContent>
          <ContaactsTab {...props} />
        </TabContent>
      </Tab>
      <Tab eventKey='chat groups' title='CHAT GROUPS'>
        {renderTabContent('CHAT GROUPS')}
      </Tab>
    </TabsStyle>
  );
};

export default MyTabs;

const TabsStyle = styled(Tabs)`
  background: #fff;
  border: none;
  margin: 0;
  padding: 0;
  > .nav-link {
    border: none !important;
    margin: 0 !important;
    color: grey;
    font-weight: bold !important;
    transistion: 1s ease-out;
    position: relative;

    &: hover {
      color: grey;
      opacity: 0.5;
    }

    &.active {
      color: blue;
      &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 2px;
        left: 0;
        bottom: 0;
        background-color: blue;
      }
    }
  }
`;

const TabContent = styled.div`
  padding: 1rem;
`;
