import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

import Card from '../Card/Card';

import { Col, Row } from 'react-bootstrap';
import { AiFillCheckCircle } from 'react-icons/ai';
import { FaMapMarkerAlt, FaTwitter, FaGlobeAmericas } from 'react-icons/fa';
import { FcCallback } from 'react-icons/fc';
import { MdUpdate } from 'react-icons/md';

const ContaactsTab = ({ contactList = [] }) => {
  const call = (phoneNumber) => {
    window.open(`tel:${phoneNumber}`);
  };

  const updatedDetails = (time_stamp = '') => {
    var d = new Date(time_stamp);
    d.setMinutes(d.getMinutes() - 330);

    return d.toLocaleString();
  };

  return (
    <React.Fragment>
      {contactList.length ? (
        contactList.map(
          ({
            facility = '',
            status = '',
            supplier_name = '',
            person_name = '',
            phone_number = '',
            address = '',
            website = '',
            description = '',
            time_stamp = ''
          }) => (
            <Card>
              <RowStyle>
                <Col xs={8}>
                  <Title>{facility}</Title>
                </Col>
                <RightCol xs={4}>
                  <BadgeWrapper>
                    <WithIcon>
                      {status.toLowerCase() === 'verified' ? <AiFillCheckCircle /> : ''}
                      {status}
                    </WithIcon>
                  </BadgeWrapper>
                </RightCol>
              </RowStyle>
              <RowStyle>
                <Col xs={8}>
                  <Sup>{supplier_name}</Sup>
                </Col>
                <RightCol xs={4}>
                  <SocialMediaIcons>
                    <FaTwitter />
                  </SocialMediaIcons>
                </RightCol>
              </RowStyle>
              <RowStyle>
                <Col xs={8}>
                  <Name>{person_name}</Name>
                </Col>
                <RightCol xs={4}>
                  <ContactInfo href={`tel:${phone_number}`}>
                    <FcCallback />
                    {phone_number}
                  </ContactInfo>
                </RightCol>
              </RowStyle>
              <RowStyle>
                <Col sm={6}>
                  <Address>
                    <FaMapMarkerAlt /> {address}
                  </Address>
                </Col>
                <RightCol sm={6}>
                  <Mail>
                    <FaGlobeAmericas /> {website}
                  </Mail>
                </RightCol>
              </RowStyle>

              <RowStyle>
                <Col sm={6}>
                  <Desc>{description}</Desc>
                </Col>
                <RightCol sm={6}>
                  <Updated>
                    <MdUpdate /> Updated {updatedDetails(time_stamp)}
                  </Updated>
                </RightCol>
              </RowStyle>
            </Card>
          )
        )
      ) : (
        <p>No contacts found</p>
      )}
    </React.Fragment>
  );
};

export default ContaactsTab;

const Title = styled.div`
  cursor: text;
  color: powderblue;
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.3), 0px -1px 0px rgba(0, 0, 0, 0.7);
`;

const BadgeWrapper = styled.div`
  cursor: text;
  display: inline-block;
  padding: 0.2rem 0.4rem;
  border-radius: 25px;
  background-color: rgba(250, 250, 250, 1);
  svg {
    margin-right: 0.3rem;
    color: green;
  }
`;

const WithIcon = styled.span`
  display: flex;
  align-items: center;
`;

const RowStyle = styled(Row)`
  align-items: center;
  padding: 0.2rem 0;
`;

const RightCol = styled(Col)`
  text-align: right;
`;

const SocialMediaIcons = styled.div`
  color: blue;
`;

const ContactInfo = styled.a`
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
  font-weight: bold;
  color: black !important;
  text-decoration: none !important;
  > svg {
    margin-right: 0.5rem;
  }
`;

const Sup = styled.div`
  font-size: 1.1rem;
  font-weight: bold;
  opacity: 0.7;
  cursor: text;
  text-transform: capitalize;
`;

const Name = styled.div`
  font-size: 1.3rem;
  font-weight: bold;
  cursor: text;
  text-transform: capitalize;
`;

const Address = styled.div`
  display: flex;
  justify-content: flex-start;
  font-size: 1rem;
  font-weight: bold;
  opacity: 0.7;
  cursor: text;

  > svg {
    margin-right: 0.5rem;
  }
`;

const Mail = styled.div`
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
  font-weight: bold;
  cursor: text;
  opacity: 0.7;
  > svg {
    margin-right: 0.5rem;
  }
`;

const Desc = styled.div`
  font-size: 0.9rem;
  font-style: italic;
  line-height: 1;
  color: black;
  opacity: 0.7;
  padding: 0.5rem 0;
`;

const Updated = styled.div`
  cursor: text;
  display: flex;
  justify-content: flex-end;
  font-size: 0.9rem;
  font-style: italic;
  line-height: 1;
  color: grey;
  opacity: 0.9;
  > svg {
    margin-right: 0.5rem;
  }
`;
